const weekDays = ['', '', '', '', '', '', ''];

const days = ['', '', '', '', '', '', '', '', '', ''];

const formatData = (date: string): string => {
  const currentDate = new Date();
  const orderDate = new Date(date);

  const daysFromOrder: number = Math.floor((currentDate.getTime() - orderDate.getTime()) / 1000 / 3600 / 24);
  const time = `${orderDate.getHours()}:${orderDate.getMinutes()}`;
  const remains: number = daysFromOrder % 10;

  if (daysFromOrder < 1) {
    return `  ${time}`;
  }
  if (daysFromOrder < 2) {
    return `  ${time}`;
  }
  if (daysFromOrder < 7) {
    return ` ${weekDays[orderDate.getDay() - 1]}  ${time}`;
  }
  if (daysFromOrder <= 20) {
    return `${daysFromOrder}    ${time}`;
  }
  return `${daysFromOrder} ${days[remains]}   ${time}`;
};

export default formatData;
